import * as React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';

import { alpha, styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import Tooltip from '@mui/material/Tooltip';
import TableCell from '@mui/material/TableCell';
import ListItemText from '@mui/material/ListItemText';
import MenuItem from '@mui/material/MenuItem';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterListOffIcon from '@mui/icons-material/FilterListOff';
import { visuallyHidden } from '@mui/utils';

import Loader from '../components/Loader';

import { LoadsQuery, LoadDelete } from '../api';
import { useSnackbar } from '../hooks';
import {
  Order,
  Load,
  LoadStatuses,
  LoadStatus,
  StopType,
  StopPickUpStatuses,
  StopDeliveryStatuses,
} from '../utils/dataTypes';
import { stopPropagation } from '../utils/stopPropagation';
import LocationForGrid from '../components/LocationForGrid';
import { dateTimeFormatter } from '../utils/dateTimeFormatters';
import { fromISOCorrected } from '../utils/dateTimeConverters';

interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Load;
  getData: (row: Load) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'loadNumber',
    orderField: 'loadNumber',
    getData: (row) => ({ data: `${row.loadNumber}` }),
    numeric: true,
    disablePadding: false,
    label: 'Load Number',
  },
  {
    id: 'ref',
    getData: (row) => {
      const firstRow = `${row?.ref && row?.ref[0] ? row?.ref[0] : ''}`;
      const secondRow = `${row?.ref && row?.ref[1] ? row?.ref[1] : ''}`;
      const thirdRow = `${row?.ref && row?.ref[2] ? row?.ref[2] : ''}`;

      return {
        data: (
          <span>
            {firstRow}
            <br />
            {secondRow}
            <br />
            {thirdRow}
          </span>
        ),
        title:
          firstRow +
          (secondRow ? '\n' + secondRow : '') +
          (thirdRow ? '\n' + thirdRow : ''),
      };
    },
    numeric: false,
    disablePadding: false,
    label: 'REF Number',
  },
  {
    id: 'firstPickUp',
    getData: (row) => {
      const onRefClick = (event: React.SyntheticEvent) => {
        event.preventDefault();
        stopPropagation(event);
      };
      const firstStop = row?.stops?.at(0);
      const facility = firstStop?.facility;
      const location = facility?.facilityLocation;
      const timeFrame = firstStop?.timeFrame;
      const timeFrameType = timeFrame?.type;
      const timeFrameStart =
        timeFrame?.type === 'FCFS'
          ? (timeFrame?.from as unknown as string)
          : (timeFrame?.at as unknown as string);
      const timeFrameEnd =
        timeFrame?.type === 'FCFS'
          ? (timeFrame?.to as unknown as string)
          : null;

      const timeFrameStartString = `${
        timeFrameStart
          ? dateTimeFormatter.format(fromISOCorrected(timeFrameStart))
          : ''
      }`;
      const timeFrameEndString = `${
        timeFrameEnd
          ? dateTimeFormatter.format(fromISOCorrected(timeFrameEnd))
          : ''
      }`;

      const secondRow = `${timeFrameType} ${timeFrameStartString}${timeFrameEndString ? ' - ' + timeFrameEndString : ''}`;

      if (location) {
        return {
          data: (
            <LocationForGrid
              location={location}
              onRefClick={onRefClick}
              addInfo={secondRow}
            />
          ),
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: false,
    label: 'First PickUp',
  },
  {
    id: 'lastDelivery',
    getData: (row) => {
      const onRefClick = (event: React.SyntheticEvent) => {
        event.preventDefault();
        stopPropagation(event);
      };
      const firstStop = row?.stops?.at(-1);
      const facility = firstStop?.facility;
      const location = facility?.facilityLocation;
      const timeFrame = firstStop?.timeFrame;
      const timeFrameType = timeFrame?.type;
      const timeFrameStart =
        timeFrame?.type === 'FCFS'
          ? (timeFrame?.to as unknown as string)
          : (timeFrame?.at as unknown as string);
      const timeFrameEnd =
        timeFrame?.type === 'FCFS'
          ? (timeFrame?.to as unknown as string)
          : null;

      const timeFrameStartString = `${
        timeFrameStart
          ? dateTimeFormatter.format(fromISOCorrected(timeFrameStart))
          : ''
      }`;
      const timeFrameEndString = `${
        timeFrameEnd
          ? dateTimeFormatter.format(fromISOCorrected(timeFrameEnd))
          : ''
      }`;

      const secondRow = `${timeFrameType} ${timeFrameStartString}${timeFrameEndString ? ' - ' + timeFrameEndString : ''}`;

      if (location) {
        return {
          data: (
            <LocationForGrid
              location={location}
              onRefClick={onRefClick}
              addInfo={secondRow}
            />
          ),
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: false,
    label: 'Last Delivery',
  },
  {
    id: 'miles',
    getData: (row) => {
      if (row?.milesHaversine) {
        const firstRow = `${
          row.milesByRoads ? row.milesByRoads.toFixed(2) : ''
        }`;
        const secondRow = `${row.milesHaversine.toFixed(2)}`;

        return {
          data: (
            <span>
              <b>{firstRow}</b>
              <br />
              {secondRow}
            </span>
          ),
          title: firstRow
            ? firstRow
            : '?' + ' - By roads' + '\n' + secondRow + ' - Distance',
        };
      } else {
        return { data: '' };
      }
    },
    numeric: true,
    disablePadding: false,
    label: 'Miles',
  },
  {
    id: 'status',
    orderField: 'status',
    getData: (row) => {
      const firstRow = `${row.status}`;
      const stopInNonFinalStatus = row.stops.find(
        (stop) =>
          (stop.type === StopType.PickUp &&
            stop.status !== StopPickUpStatuses.at(0) &&
            stop.status !== StopPickUpStatuses.at(-1)) ||
          (stop.type === StopType.Delivery &&
            stop.status !== StopDeliveryStatuses.at(0) &&
            stop.status !== StopDeliveryStatuses.at(-1)),
      );
      const secondRow = `${stopInNonFinalStatus ? stopInNonFinalStatus?.status : ''}`;

      return {
        data: (
          <span>
            <b>{firstRow}</b>
            <br />
            {secondRow}
          </span>
        ),
        title: !secondRow ? firstRow : firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: false,
    label: 'Load Status',
  },
  {
    id: 'truck',
    getData: (row) => {
      if (row?.truck) {
        const path = `./trucks/view/${row.truck.id}`;
        const firstRow = `${row.truck.truckNumber}`;

        return {
          data: (
            <span>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {firstRow}
              </a>
            </span>
          ),
          title: firstRow,
        };
      } else {
        return { data: '' };
      }
    },
    numeric: true,
    disablePadding: false,
    label: 'Truck',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Load,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)(() => ({
  maxWidth: '100px',
  paddingTop: '6px',
  paddingBottom: '6px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Load) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell padding="checkbox">
          <Checkbox
            name={'enhanced-table-checkbox-select-all'}
            color="primary"
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{
              'aria-label': 'enhanced-table-checkbox-select-all',
            }}
          />
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

interface EnhancedTableToolbarProps {
  selected: readonly string[];
  setQueryParams: React.Dispatch<
    React.SetStateAction<Record<string, number | string | string[]>>
  >;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { selected, setQueryParams } = props;
  const [refNumberSearch, setRefNumberSearch] = React.useState<string>('');
  const [statusOpen, setStatusOpen] = React.useState<boolean>(false);
  const [statusValue, setStatusValue] = React.useState<LoadStatus[]>([
    ...LoadStatuses,
  ]);
  const [loadNumberSearch, setLoadNumberSearch] = React.useState<number>(0);
  const [truckNumberSearch, setTruckNumberSearch] = React.useState<number>(0);
  const [searchDataChanged, setSearchDataChanged] =
    React.useState<boolean>(false);

  const location = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();

  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(LoadDelete, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Deleted',
      });
      queryClient.invalidateQueries(['loads']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const refNumberHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRefNumberSearch(event.target.value);
    setSearchDataChanged(true);
  };
  const handleStatusInputChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setStatusValue(event.target.value as unknown as LoadStatus[]);
    setSearchDataChanged(true);
  };
  const handleStatusKeyUp = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    stopPropagation(event);
    handleStatusClose();
  };
  const handleStatusKeyDown = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    stopPropagation(event);
  };
  const handleStatusOpen = () => {
    setStatusOpen(true);
  };
  const handleStatusClose = () => {
    setStatusOpen(false);
    handleStatusChange();
  };
  const handleStatusChange = () => {
    if (statusValue.length && statusValue.length !== LoadStatuses.length) {
      setQueryParams((prev) => ({ ...prev, status: statusValue }));
      setSearchDataChanged(false);
    } else {
      setQueryParams((prev) => {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        const { status: _, ...rest } = prev;
        return { ...rest };
      });
      setSearchDataChanged(false);
    }
  };
  const loadNumberHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (Number.isFinite(Number(event.target.value))) {
      setLoadNumberSearch(Number(event.target.value));
      setSearchDataChanged(true);
    }
  };
  const truckNumberHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (Number.isFinite(Number(event.target.value))) {
      setTruckNumberSearch(Number(event.target.value));
      setSearchDataChanged(true);
    }
  };
  const filterClearHandler = () => {
    setSearchDataChanged(false);
    setRefNumberSearch('');
    setStatusValue([...LoadStatuses]);
    setLoadNumberSearch(0);
    setTruckNumberSearch(0);
    setQueryParams({});
  };
  const filterHandler = () => {
    setSearchDataChanged(false);
    const queryParams: Record<string, number | string | string[]> = {};
    if (refNumberSearch) {
      queryParams.refNumber = refNumberSearch;
    }
    if (statusValue.length && statusValue.length !== LoadStatuses.length) {
      queryParams.status = statusValue;
    }
    if (loadNumberSearch) {
      queryParams.loadNumber = loadNumberSearch;
    }
    if (truckNumberSearch) {
      queryParams.truckNumber = truckNumberSearch;
    }
    setQueryParams(queryParams);
  };
  const keyUpHandler = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    if (refNumberSearch || loadNumberSearch || truckNumberSearch) {
      filterHandler();
    } else {
      filterClearHandler();
    }
  };
  const createHandler = () => {
    navigate('./create', {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const editHandler = () => {
    navigate(`./edit/${selected[0]}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const deleteHandler = () => {
    mutate(`${selected[0]}`);
  };

  return (
    <Toolbar
      sx={{
        overflow: 'hidden',
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(selected.length > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity,
            ),
        }),
      }}
    >
      {selected.length > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%' }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {selected.length} selected
        </Typography>
      ) : (
        <Typography
          sx={{
            flex: '1 1 100%',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
          }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          Loads
        </Typography>
      )}
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          label="REF Number"
          value={refNumberSearch || ''}
          onChange={refNumberHandler}
          onKeyUp={keyUpHandler}
          size="small"
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          select
          SelectProps={{
            multiple: true,
            open: statusOpen,
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            //@ts-ignore
            renderValue: (selected: LoadStatuses[]) => selected.join(', '),
            onOpen: handleStatusOpen,
            onClose: handleStatusClose,
          }}
          label="Select Status"
          value={statusValue}
          onChange={handleStatusInputChange}
          onKeyUpCapture={handleStatusKeyUp}
          onKeyDownCapture={handleStatusKeyDown}
          onBlur={handleStatusChange}
          size="small"
          fullWidth
          title={statusValue.join(', ')}
        >
          {LoadStatuses.map((status) => (
            <MenuItem key={status} value={status}>
              <Checkbox
                checked={statusValue.indexOf(status as LoadStatus) > -1}
                id={status}
              />
              <ListItemText primary={status} />
            </MenuItem>
          ))}
        </TextField>
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          label="Load Number"
          value={loadNumberSearch || ''}
          onChange={loadNumberHandler}
          onKeyUp={keyUpHandler}
          size="small"
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          label="Truck Number"
          value={truckNumberSearch || ''}
          onChange={truckNumberHandler}
          onKeyUp={keyUpHandler}
          size="small"
        />
      </Box>
      <Tooltip title="Filter list">
        {(searchDataChanged ||
          (!refNumberSearch &&
            !loadNumberSearch &&
            !truckNumberSearch &&
            !(
              statusValue.length && statusValue.length !== LoadStatuses.length
            ))) &&
        (!(searchDataChanged && !refNumberSearch) ||
          !(
            searchDataChanged &&
            !(statusValue.length && statusValue.length !== LoadStatuses.length)
          ) ||
          !(searchDataChanged && !loadNumberSearch) ||
          !(searchDataChanged && !truckNumberSearch)) ? (
          <span>
            <IconButton
              disabled={!loadNumberSearch && !truckNumberSearch}
              onClick={filterHandler}
            >
              <FilterListIcon />
            </IconButton>
          </span>
        ) : (
          <span>
            <IconButton onClick={filterClearHandler}>
              <FilterListOffIcon />
            </IconButton>
          </span>
        )}
      </Tooltip>
      <Box sx={{ p: 1, display: 'inline-flex' }}>
        <Tooltip title="Create">
          <span>
            <IconButton onClick={createHandler}>
              <AddIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Edit">
          <span>
            <IconButton disabled={selected.length !== 1} onClick={editHandler}>
              <EditIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Delete">
          <span>
            <IconButton
              disabled={selected.length !== 1}
              onClick={deleteHandler}
            >
              <DeleteIcon />
            </IconButton>
          </span>
        </Tooltip>
      </Box>
      <Loader visible={isLoading} />
    </Toolbar>
  );
}

export default function EnhancedTable() {
  const [order, setOrder] = React.useState<Order | undefined>('desc');
  const [orderBy, setOrderBy] = React.useState<keyof Load | undefined>(
    'loadNumber',
  );
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = React.useState<number>(15);
  const [queryParams, setQueryParams] = React.useState<Record<string, any>>({});
  const tableContainerRef = React.useRef(null);
  const location = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading,
    isFetching,
    error,
    data: { items, total } = { items: [], total: 0 },
  } = useQuery({
    queryKey: ['loads', page, rowsPerPage, orderBy, order, queryParams],
    queryFn: LoadsQuery,
    keepPreviousData: true,
    retry: false,
  });
  React.useEffect(() => {
    if (!error) {
      return;
    }
    setSnackbarMessage({
      severity: 'error',
      message: error instanceof Error ? error.message : JSON.stringify(error),
    });
  }, [error]);
  const rows: Load[] = items || [];

  React.useEffect(() => {
    if (tableContainerRef.current) {
      const container = tableContainerRef.current as HTMLElement;
      container.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    }
  }, [rows]);

  React.useEffect(() => {
    const newSelected: string[] = [];
    selected.forEach((selectedItem) => {
      const selectedRow = rows.find((row) => row.id === selectedItem);
      if (selectedRow) {
        newSelected.push(selectedItem);
      }
    });
    setSelected(newSelected);
  }, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Load,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    const isDesc = orderBy === property && order === 'desc';
    if (isDesc) {
      setOrder(undefined);
      setOrderBy(undefined);
    } else {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(property);
    }
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n: Load) => n.id);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleClick = (
    event: React.MouseEvent<unknown>,
    item: (typeof rows)[number],
  ) => {
    navigate(`./view/${item.id}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };

  const handleSelect = (event: React.MouseEvent<unknown>, name: string) => {
    event.stopPropagation();
    const selectedIndex = selected.indexOf(name);
    let newSelected: readonly string[] = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  return (
    <Box sx={{ width: '100%', height: '100%' }}>
      <Paper sx={{ width: '100%', height: '100%', backgroundColor: 'unset' }}>
        <EnhancedTableToolbar
          selected={selected}
          setQueryParams={(query) => {
            setQueryParams(query);
            setPage(0);
          }}
        />
        <TableContainer
          ref={tableContainerRef}
          sx={{ height: 'calc(100% - 115px)' }}
        >
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size="small"
            stickyHeader
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(`${row.id}`);
                const labelId = `enhanced-table-checkbox-${index}`;
                return (
                  <TableRow
                    hover
                    onDoubleClick={(event) => handleClick(event, row)}
                    role="checkbox"
                    aria-checked={isItemSelected}
                    tabIndex={-1}
                    key={row.id}
                    selected={isItemSelected}
                    sx={{ cursor: 'pointer' }}
                  >
                    <StyledTableCell padding="checkbox">
                      <Checkbox
                        id={`${labelId}`}
                        color="primary"
                        checked={isItemSelected}
                        inputProps={{
                          'aria-labelledby': labelId,
                        }}
                        onClick={(event) => handleSelect(event, `${row.id}`)}
                      />
                    </StyledTableCell>
                    {headCells.map((cell) => {
                      const cellData = cell.getData(row);
                      return (
                        <StyledTableCell
                          key={cell.id}
                          title={cellData.title}
                          align={cell.numeric ? 'right' : 'center'}
                        >
                          {cellData.data}
                        </StyledTableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15, 20, 25, 35, 50]}
          component="div"
          count={total}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <Loader visible={isLoading || isFetching} />
    </Box>
  );
}
